import React, { useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AuthContext } from "../../AuthProvider";
import Logout from "../logout/Logout";
import firebase from "firebase/app";
import "./Navbar.css";

function Navbar() {
  const { currentUser } = useContext(AuthContext);
  const navigate = useNavigate();

  const signOut = () => {
    firebase
      .auth()
      .signOut()
      .then(() => {
        navigate("/login");
      });
  };

  //    Navbar .
  if (currentUser) {
    return (
      <nav className="fixed-top pt-0">
        <div className="navAlign">
          <a href="/" role="button">
            <img className="logo" src="/eagleLogo.svg" alt="clubsmate logo" />
          </a>
          <div className="menu-tab">
            <div className="menu-items">
              <Link to="/" className="nav-item">
                Home
              </Link>
              <Link to="/announcement" className="nav-item">
                Announcements
              </Link>
              <a href="mailto:eaglecouncil@sas.edu.sg" className="nav-item">
                Contact Us
              </a>
            </div>
          </div>

          <div className="user-controls">
            <div className="nav-link-box">
              <Link
                to="/mypage"
                style={{ textDecoration: "none", color: "inherit" }}
              >
                My Page
              </Link>
            </div>
            <div className="nav-link-box" onClick={signOut}>
              Sign out
            </div>
          </div>
        </div>
      </nav>
    );
  } else {
    //      .
    return null;
  }
}

export default Navbar;
